import { supabase } from '../supabaseClient';
import { AuthService } from './authService';
import logger from '../utils/logger';

/**
 * Servio para gerenciar versionamento de metadados
 */
export class VersionamentoService {
  
  /**
   * Obter o autor atual (usurio autenticado)
   * @returns {Promise<string|null>} Nome do usurio ou null se no autenticado
   */
  static async obterAutorAtual() {
    try {
      const isAuthenticated = await AuthService.isAuthenticated();
      if (!isAuthenticated) {
        return null;
      }
      
      const currentUser = await AuthService.getCurrentUser();
      return currentUser?.username || 'admin';
    } catch (error) {
      logger.warn('Erro ao obter autor atual:', error);
      return null;
    }
  }

  /**
   * Registrar uma nova verso de dados
   * @param {Object} params - Parmetros da verso
   * @param {string} params.nomeTabela - Nome da tabela onde ocorreu a alterao
   * @param {string} params.chaveLinha - Chave primria da linha afetada (convertida para string)
   * @param {string|null} params.nomeCampo - Nome do campo especfico alterado (opcional)
   * @param {string|null} params.fonteId - ID da fonte de dados (opcional)
   * @param {string|null} params.autor - Nome do autor (opcional, ser obtido automaticamente se no fornecido)
   * @param {string|null} params.observacoes - Observaes sobre a alterao (opcional)
   * @returns {Promise<Object>} Resultado da operao { success: boolean, data?: Object, error?: string }
   */
  static async registrarVersaoDados({
    nomeTabela,
    chaveLinha,
    nomeCampo = null,
    fonteId = null,
    autor = null,
    observacoes = null
  }) {
    try {
      // Validar parmetros obrigatrios
      if (!nomeTabela || nomeTabela.trim() === '') {
        throw new Error('nomeTabela  obrigatrio');
      }
      
      if (!chaveLinha || chaveLinha.toString().trim() === '') {
        throw new Error('chaveLinha  obrigatrio');
      }

      // Obter autor se no fornecido
      let autorFinal = autor;
      if (!autorFinal) {
        autorFinal = await this.obterAutorAtual();
      }

      // Preparar dados para insero
      const dadosVersao = {
        nome_tabela: nomeTabela.trim(),
        chave_linha: chaveLinha.toString().trim(),
        autor: autorFinal,
        criado_em: new Date().toISOString()
      };

      // Adicionar campos opcionais se fornecidos
      if (nomeCampo && nomeCampo.trim() !== '') {
        dadosVersao.nome_campo = nomeCampo.trim();
      }

      if (fonteId) {
        dadosVersao.fonte_id = fonteId;
      }

      if (observacoes && observacoes.trim() !== '') {
        dadosVersao.observacoes = observacoes.trim();
      }

      // Inserir registro de verso
      const { data, error } = await supabase
        .from('versoes_dados')
        .insert(dadosVersao)
        .select()
        .single();

      if (error) {
        logger.error('Erro ao registrar verso de dados:', error);
        throw error;
      }

      return {
        success: true,
        data: data
      };
    } catch (error) {
      logger.error('Erro no servio de versionamento:', error);
      return {
        success: false,
        error: error.message || 'Erro ao registrar verso de dados'
      };
    }
  }

  /**
   * Buscar todas as fontes de dados disponveis
   * @returns {Promise<Object>} Resultado com lista de fontes
   */
  static async buscarFontesDados() {
    try {
      const { data, error } = await supabase
        .from('fontes_dados')
        .select('id, nome, descricao')
        .order('nome', { ascending: true });

      if (error) {
        throw error;
      }

      return {
        success: true,
        data: data || []
      };
    } catch (error) {
      logger.error('Erro ao buscar fontes de dados:', error);
      return {
        success: false,
        error: error.message || 'Erro ao buscar fontes de dados',
        data: []
      };
    }
  }

  /**
   * Buscar histrico de verses para uma linha especfica
   * @param {string} nomeTabela - Nome da tabela
   * @param {string} chaveLinha - Chave primria da linha
   * @returns {Promise<Object>} Resultado com lista de verses
   */
  static async buscarVersoesPorLinha(nomeTabela, chaveLinha) {
    try {
      const { data, error } = await supabase
        .from('versoes_dados')
        .select(`
          id,
          nome_tabela,
          chave_linha,
          autor,
          observacoes,
          criado_em,
          fonte_id,
          fontes_dados:nome,
          fontes_dados:descricao
        `)
        .eq('nome_tabela', nomeTabela)
        .eq('chave_linha', chaveLinha.toString())
        .order('criado_em', { ascending: false });

      if (error) {
        throw error;
      }

      return {
        success: true,
        data: data || []
      };
    } catch (error) {
      logger.error('Erro ao buscar verses:', error);
      return {
        success: false,
        error: error.message || 'Erro ao buscar verses',
        data: []
      };
    }
  }
}

