import { supabase } from '../supabaseClient';
import logger from '../utils/logger';

// Configuraes para imagens das escolas
const ESCOLA_IMAGE_CONFIG = {
  BUCKET_NAME: 'imagens-das-escolas',
  MAX_FILE_SIZE: 5 * 1024 * 1024, // 5MB
  ALLOWED_TYPES: ['image/jpeg', 'image/jpg', 'image/png', 'image/webp', 'image/gif'],
  MAX_IMAGES_PER_SCHOOL: Infinity, // Sem limite
  MIN_DIMENSIONS: { width: 200, height: 200 }
};

// Configuraes para imagens dos professores
const PROFESSOR_IMAGE_CONFIG = {
  BUCKET_NAME: 'imagens-professores', // Usando bucket existente
  MAX_FILE_SIZE: 5 * 1024 * 1024, // 5MB
  ALLOWED_TYPES: ['image/jpeg', 'image/jpg', 'image/png', 'image/webp', 'image/gif'],
  MAX_IMAGES_PER_SCHOOL: Infinity, // Sem limite
  MIN_DIMENSIONS: { width: 200, height: 200 }
};

/**
 * Valida um arquivo de imagem
 * @param {File} file - Arquivo a ser validado
 * @param {Object} config - Configurao de validao
 * @returns {Object} Resultado da validao
 */
const validateImageFile = (file, config) => {
  // Verificar tipo MIME
  if (!config.ALLOWED_TYPES.includes(file.type)) {
    return {
      isValid: false,
      error: `Tipo de arquivo no suportado. Use apenas: ${config.ALLOWED_TYPES.map(t => t.split('/')[1].toUpperCase()).join(', ')}`
    };
  }

  // Verificar tamanho
  if (file.size > config.MAX_FILE_SIZE) {
    return {
      isValid: false,
      error: `Arquivo muito grande. Tamanho mximo: ${config.MAX_FILE_SIZE / (1024 * 1024)}MB`
    };
  }

  // Verificar extenso
  const extension = file.name.split('.').pop().toLowerCase();
  const allowedExtensions = config.ALLOWED_TYPES.map(t => t.split('/')[1]);
  if (!allowedExtensions.includes(extension)) {
    return {
      isValid: false,
      error: `Extenso no permitida. Use apenas: ${allowedExtensions.join(', ')}`
    };
  }

  return { isValid: true };
};

/**
 * Gera nome nico para o arquivo (sem gnero, para imagens de escola)
 * @param {File} file - Arquivo
 * @param {number} escolaId - ID da escola
 * @returns {string} Nome nico do arquivo
 */
const generateUniqueFileName = (file, escolaId) => {
  const timestamp = Date.now();
  const random = Math.random().toString(36).substring(2, 8);
  const extension = file.name.split('.').pop().toLowerCase();
  return `${escolaId}_${timestamp}_${random}.${extension}`;
};

/**
 * Gera nome nico para o arquivo, incluindo o gnero
 * @param {File} file - Arquivo
 * @param {number} escolaId - ID da escola
 * @param {string} genero - 'professor' ou 'professora'
 * @returns {string} Nome nico do arquivo
 */
const generateUniqueFileNameWithGenero = (file, escolaId, genero) => {
  const timestamp = Date.now();
  const random = Math.random().toString(36).substring(2, 8);
  const extension = file.name.split('.').pop().toLowerCase();
  return `${genero}_${escolaId}_${timestamp}_${random}.${extension}`;
};

/**
 * Upload de imagem da escola (verso simplificada sem tabela de metadados)
 * @param {File} file - Arquivo de imagem
 * @param {number} escolaId - ID da escola
 * @param {string} descricao - Descrio da imagem
 * @returns {Promise<Object>} Dados da imagem salva
 */
export const uploadEscolaImage = async (file, escolaId, descricao = '') => {
  try {
    // Validar arquivo
    const validation = validateImageFile(file, ESCOLA_IMAGE_CONFIG);
    if (!validation.isValid) {
      throw new Error(validation.error);
    }

    // Gerar nome nico
    const fileName = generateUniqueFileName(file, escolaId);
    const filePath = `${escolaId}/${fileName}`;

    // Upload ao bucket
    const { error: uploadError } = await supabase.storage
      .from(ESCOLA_IMAGE_CONFIG.BUCKET_NAME)
      .upload(filePath, file, {
        cacheControl: '3600',
        upsert: false
      });

    if (uploadError) {
      throw new Error(`Erro no upload: ${uploadError.message}`);
    }

    // Obter URL pblica
    const { data: { publicUrl } } = supabase.storage
      .from(ESCOLA_IMAGE_CONFIG.BUCKET_NAME)
      .getPublicUrl(filePath);

    return {
      id: Date.now(), // ID temporrio
      url: filePath,
      publicUrl,
      descricao: descricao.trim() || `Imagem da escola ${escolaId}`,
      created_at: new Date().toISOString()
    };

  } catch (error) {
    logger.error('Erro no upload da imagem da escola:', error);
    throw error;
  }
};

/**
 * Upload de imagem do professor (com gnero no nome do arquivo)
 * @param {File} file - Arquivo de imagem
 * @param {number} escolaId - ID da escola
 * @param {string} descricao - Descrio da imagem
 * @param {string} genero - 'professor' ou 'professora'
 * @returns {Promise<Object>} Dados da imagem salva
 */
export const uploadProfessorImage = async (file, escolaId, descricao = '', genero = 'professor') => {
  try {
    // Validar arquivo
    const validation = validateImageFile(file, PROFESSOR_IMAGE_CONFIG);
    if (!validation.isValid) {
      throw new Error(validation.error);
    }

    // Gerar nome nico com gnero
    const fileName = generateUniqueFileNameWithGenero(file, escolaId, genero);
    const filePath = `${escolaId}/${fileName}`;

    // Upload ao bucket
    const { error: uploadError } = await supabase.storage
      .from(PROFESSOR_IMAGE_CONFIG.BUCKET_NAME)
      .upload(filePath, file, {
        cacheControl: '3600',
        upsert: false
      });

    if (uploadError) {
      logger.error('Erro detalhado do upload:', uploadError);
      throw new Error(`Erro no upload: ${uploadError.message}`);
    }

    // Obter URL pblica
    const { data: { publicUrl } } = supabase.storage
      .from(PROFESSOR_IMAGE_CONFIG.BUCKET_NAME)
      .getPublicUrl(filePath);

    // Retornar objeto simulado (sem metadados na tabela)
    return {
      id: Date.now(), // ID temporrio
      url: filePath,
      publicUrl,
      descricao: descricao.trim() || `Imagem do ${genero} da escola ${escolaId}`,
      genero,
      created_at: new Date().toISOString()
    };

  } catch (error) {
    logger.error('Erro no upload da imagem do professor:', error);
    throw error;
  }
};

/**
 * Buscar imagens da escola (verso simplificada)
 * @param {number} escolaId - ID da escola
 * @param {string} bucketName - Nome do bucket
 * @returns {Promise<Array>} Lista de imagens
 */
export const getEscolaImages = async (escolaId, bucketName = ESCOLA_IMAGE_CONFIG.BUCKET_NAME) => {
  try {
    // Listar arquivos no bucket para a escola especfica
    const { data: files, error } = await supabase.storage
      .from(bucketName)
      .list(`${escolaId}/`);

    if (error) {
      throw error;
    }

    if (!files || files.length === 0) {
      return [];
    }

    // Criar objetos de imagem com URLs pblicas
    const imagens = files.map((file, index) => {
      const filePath = `${escolaId}/${file.name}`;
      const { data: { publicUrl } } = supabase.storage
        .from(bucketName)
        .getPublicUrl(filePath);

      return {
        id: index + 1, // ID temporrio
        url: filePath,
        publicUrl,
        descricao: `Imagem ${index + 1}`,
        created_at: file.created_at || new Date().toISOString()
      };
    });

    return imagens;

  } catch (error) {
    logger.error('Erro ao buscar imagens da escola:', error);
    throw error;
  }
};

/**
 * Deletar imagem (verso simplificada)
 * @param {number} imageId - ID da imagem
 * @param {string} filePath - Caminho do arquivo
 * @param {string} bucketName - Nome do bucket
 * @returns {Promise<void>}
 */
export const deleteImage = async (imageId, filePath, bucketName) => {
  try {
    // Deletar arquivo do storage
    const { error: storageError } = await supabase.storage
      .from(bucketName)
      .remove([filePath]);

    if (storageError) {
      throw new Error(`Erro ao deletar arquivo: ${storageError.message}`);
    }

    // Verificar se o arquivo realmente saiu da lista
    const directory = filePath.includes('/') ? filePath.split('/')[0] + '/' : '';
    const { data: files, error: listError } = await supabase.storage
      .from(bucketName)
      .list(directory);
    if (listError) {
      // No bloquear, mas logar
      logger.warn('Aviso: falha ao listar aps deleo:', listError);
    } else if (files && files.some(f => `${directory}${f.name}` === filePath)) {
      throw new Error('Arquivo ainda presente no bucket aps tentativa de excluso. Verifique permisses do bucket.');
    }

    return true;
  } catch (error) {
    logger.error('Erro ao deletar imagem:', error);
    throw error;
  }
};

/**
 * Atualizar descrio da imagem (verso simplificada)
 * @param {number} imageId - ID da imagem
 * @param {string} descricao - Nova descrio
 * @returns {Promise<Object>} Imagem atualizada
 */
export const updateImageDescription = async (imageId, descricao) => {
  try {
    // Como no temos tabela de metadados, retornamos um objeto simulado
    return {
      id: imageId,
      descricao: descricao.trim()
    };

  } catch (error) {
    logger.error('Erro ao atualizar descrio da imagem:', error);
    throw error;
  }
};

/**
 * Substituir imagem existente (trocar imagem)
 * @param {File} newFile - Novo arquivo de imagem
 * @param {string} oldFilePath - Caminho do arquivo antigo
 * @param {number} escolaId - ID da escola
 * @param {string} bucketName - Nome do bucket
 * @param {string} descricao - Descrio da nova imagem
 * @returns {Promise<Object>} Dados da nova imagem
 */
export const replaceImage = async (newFile, oldFilePath, escolaId, bucketName, descricao = '') => {
  try {
    const config = bucketName === PROFESSOR_IMAGE_CONFIG.BUCKET_NAME 
      ? PROFESSOR_IMAGE_CONFIG 
      : ESCOLA_IMAGE_CONFIG;

    // Validar novo arquivo
    const validation = validateImageFile(newFile, config);
    if (!validation.isValid) {
      throw new Error(validation.error);
    }

    // Gerar nome nico para o novo arquivo
    const fileName = bucketName === PROFESSOR_IMAGE_CONFIG.BUCKET_NAME
      ? generateUniqueFileNameWithGenero(newFile, escolaId, 'professor') // Assumindo professor por padro
      : generateUniqueFileName(newFile, escolaId);
    
    const newFilePath = `${escolaId}/${fileName}`;

    // 1. Upload da nova imagem
    const { error: uploadError } = await supabase.storage
      .from(bucketName)
      .upload(newFilePath, newFile, {
        cacheControl: '3600',
        upsert: false
      });

    if (uploadError) {
      throw new Error(`Erro no upload da nova imagem: ${uploadError.message}`);
    }

    // 2. Obter URL pblica da nova imagem
    const { data: { publicUrl } } = supabase.storage
      .from(bucketName)
      .getPublicUrl(newFilePath);

    // 3. Deletar a imagem antiga
    const { error: deleteError } = await supabase.storage
      .from(bucketName)
      .remove([oldFilePath]);

    if (deleteError) {
      logger.warn('Erro ao deletar imagem antiga:', deleteError);
      // No falha a operao se no conseguir deletar a imagem antiga
    }

    return {
      id: Date.now(), // ID temporrio
      url: newFilePath,
      publicUrl,
      descricao: descricao.trim() || `Imagem da escola ${escolaId}`,
      created_at: new Date().toISOString()
    };

  } catch (error) {
    logger.error('Erro ao substituir imagem:', error);
    throw error;
  }
};

/**
 * Verificar limite de imagens (verso simplificada)
 * @param {number} escolaId - ID da escola
 * @param {string} bucketName - Nome do bucket
 * @returns {Promise<Object>} Informaes sobre o limite
 */
export const checkImageLimit = async (escolaId, bucketName) => {
  try {
    const config = bucketName === PROFESSOR_IMAGE_CONFIG.BUCKET_NAME 
      ? PROFESSOR_IMAGE_CONFIG 
      : ESCOLA_IMAGE_CONFIG;

    // Listar arquivos no bucket para a escola especfica
    const { data: files, error } = await supabase.storage
      .from(bucketName)
      .list(`${escolaId}/`);

    if (error) {
      throw error;
    }

    const currentCount = files ? files.length : 0;

    return {
      current: currentCount,
      limit: config.MAX_IMAGES_PER_SCHOOL,
      canUpload: currentCount < config.MAX_IMAGES_PER_SCHOOL
    };

  } catch (error) {
    logger.error('Erro ao verificar limite de imagens:', error);
    throw error;
  }
}; 